import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { ToastrService } from 'ngx-toastr';
import { Router } from '@angular/router';
import { OrderService } from '../../../../services/order/order.service';

@Component({
  selector: 'app-all-order',
  templateUrl: './all-order.component.html',
  styleUrls: ['./all-order.component.scss'],
})
export class AllOrderComponent implements OnInit {
  constructor(
    private spinner: NgxSpinnerService,
    private toastr: ToastrService,
    private router: Router,
    private orderService: OrderService
  ) {
    // this.socketService.setupSocketConnection();
    this.config = {
      itemsPerPage: 5,
      currentPage: 1,
      // totalItems: this.customerData.length,
    };

    // this.customerData = this.customerData.slice();
    // this.getUsersSuggestion();
  }

  ngOnInit(): void {
    this.getOrders();
    // this.getAdminUsers();
  }

  orders: any = [
    {
      date: '09-05-2021',
      id: '31',
      status: 'complete',
      orderdetail: 'http://www.com',
      amount: 30,
      deliverymethod: 'delivery',
    },
    {
      date: '09-05-2021',
      id: '31',
      status: 'complete',
      orderdetail: 'http://www.com',
      amount: 30,
      deliverymethod: 'delivery',
    },
    {
      date: '09-05-2021',
      id: '31',
      status: 'complete',
      orderdetail: 'http://www.com',
      amount: 30,
      deliverymethod: 'delivery',
    },
  ];

  fields: any = [
    { label: 'ORDER ID', fieldName: 'id' },
    { label: 'DATE', fieldName: 'createdAt' },
    // { label: 'STATUS', fieldName: 'status' },
    { label: 'ORDER DETAIL', fieldName: 'orderdetail' },
    { label: 'DELIVERY MATHOD', fieldName: 'dispatchType' },
    { label: 'Rating', fieldName: 'rating' },
    { label: 'AMOUNT', fieldName: 'totalPrice' },
    { label: 'Transaction', fieldName: 'transaction' },
  ];
  p: number = 1;
  config: any;
  deliveryStatus = 'Not-Delivered';
  pageSizes = [1, 2, 5, 10, 25, 50];
  sort = {
    orderBy: 'createdAt',
    sort: 'DESC',
  };

  navigateToOrderDetail(id) {
    this.router.navigate([`admin/order/order-detail`, id]);
  }

  pageChanged(event) {
    this.config.currentPage = event;
    this.getOrders();
  }

  changePageSize() {
    this.getOrders();
  }

  library: any;
  keyword = 'name';
  search = [];

  selectEvent(item) {
    this.search = [item];
    // // do something with selected item
    // // this.products[index].productLibraryId = item.product_id;
    // // this.products[index].productName = item.product_name;
    // console.log(item)
    // if(this.searchBy === 'Products'){
    //   console.log('Products')
    //   this.productCatalogService.setSearch(item)
    //   this.router.navigate(['store/search-products']);
    // }
    // else if(this.searchBy === 'Shop'){
    //   console.log('Shop')
    //   this.productCatalogService.setShop(item)
    //   this.router.navigate(['store/shop-products']);
    // }
  }

  onChangeSearch(search: string) {
    // this.sendUserSearch(search);
  }
  onFocused(e) {
    // do something
  }

  onEnter(e) {
    const userIds = [];
    this.search.map((item) => {
      userIds.push(item.id);
    });
    this.config['userIds'] = userIds;
    console.log(this.config);
    this.getOrders();
  }

  sortBy(orderBy, sort) {
    this.sort = {
      orderBy,
      sort,
    };
    this.getOrders();
  }

  getOrders() {
    // this.spinner.show();
    // const filter = {};
    this.orderService.getOrders(this.config, this.deliveryStatus, this.sort).subscribe(
      (data) => {
        console.log(data);
        this.orders = data.data;
        this.config.totalItems = data.total;
        this.spinner.hide();
      },
      (error) => {
        this.toastr.error(error.error.message, 'Error');
        this.spinner.hide();
      }
    );
  }

  changeSection(value) {
    this.deliveryStatus = value;
    this.getOrders();
  }
}
